@import url(https://fonts.googleapis.com/css2?family=Varela+Round&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Varela Round', sans-serif;
  height: 100vh;
}

.search-controls {
  margin-top: 20px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
.loader {
  font-size: 10px;
  margin: 50px auto;
  text-indent: -9999em;
  width: 11em;
  height: 11em;
  border-radius: 50%;
  background: #77009e;
  background: linear-gradient(to right, #77009e 10%, rgba(158,0,0, 0) 42%);
  position: relative;
  -webkit-animation: load3 1.4s infinite linear;
  animation: load3 1.4s infinite linear;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
}
.loader:before {
  width: 50%;
  height: 50%;
  background: #77009e;
  border-radius: 100% 0 0 0;
  position: absolute;
  top: 0;
  left: 0;
  content: '';
}
.loader:after {
  background: #fff;
  width: 75%;
  height: 75%;
  border-radius: 50%;
  content: '';
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}
@-webkit-keyframes load3 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes load3 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

.page-controls{text-align:center;margin:10px 50px}.page-controls button{border:none;padding:10px 20px;background-color:#770f7d92;cursor:pointer}.page-controls button:first-child{border-radius:20px 0 0 20px}.page-controls button:first-child:focus{background-color:#770f7d92}.page-controls button:last-child{border-radius:0 20px 20px 0}.page-controls button:last-child:focus{background-color:#770f7d92}.page-controls button:hover{background-color:#770f7d}.page-controls button:focus{background-color:#e8e8e8}

.table{margin:2rem auto;border-spacing:0;text-align:center;border-spacing:0  1rem}.table .table-head{color:#fff;background:linear-gradient(18deg, #c154d5 25%, #9025ab 75%)}.table .table-head tr td{position:relative;font-weight:500;padding:1rem 2rem}.table .table-head tr td:first-child{border-radius:20px 0 0 20px}.table .table-head tr td:last-child{border-radius:0 20px 20px 0}.table .table-head tr td .sort-icon,.table .table-head tr td .sort-icon_asc,.table .table-head tr td .sort-icon_desc{position:absolute;top:1.4rem;right:1rem;border-top:5px solid transparent;border-bottom:5px solid transparent;border-left:10px solid #fff;transition:-webkit-transform 0.7s ease-in;transition:transform 0.7s ease-in;transition:transform 0.7s ease-in, -webkit-transform 0.7s ease-in;cursor:pointer}.table .table-head tr td .sort-icon:hover,.table .table-head tr td .sort-icon_asc:hover,.table .table-head tr td .sort-icon_desc:hover{border-left:10px solid #50f}.table .table-head tr td .sort-icon_asc{-webkit-transform:rotate(90deg);transform:rotate(90deg);transition:-webkit-transform 0.7s ease-in;transition:transform 0.7s ease-in;transition:transform 0.7s ease-in, -webkit-transform 0.7s ease-in}.table .table-head tr td .sort-icon_desc{-webkit-transform:rotate(-90deg);transform:rotate(-90deg);transition:-webkit-transform 0.7s ease-in;transition:transform 0.7s ease-in;transition:transform 0.7s ease-in, -webkit-transform 0.7s ease-in}.table .table-body .table-row:nth-child(odd){background:linear-gradient(18deg, rgba(193,84,213,0.3) 25%, rgba(144,37,171,0.3) 75%)}.table .table-body .table-row:nth-child(even){background:linear-gradient(18deg, rgba(142,140,143,0.3) 25%, rgba(194,189,194,0.3) 75%)}.table .table-body tr td{padding:0.5rem}.table .table-body .not-found-icon{width:100px}

.search-panel{width:20vw;display:flex;align-items:center;justify-content:center;border-radius:20px;background-color:#770f7d}.search-panel svg path{fill:#fff}.search-panel .search-input{border:none;padding:1rem 2rem;background-color:#770f7d;color:#fff;outline:none}.search-panel .search-input::-webkit-input-placeholder{color:#fff;font-size:16px;font-weight:500}.search-panel .search-input:-ms-input-placeholder{color:#fff;font-size:16px;font-weight:500}.search-panel .search-input::placeholder{color:#fff;font-size:16px;font-weight:500}

.filter-panel{position:relative;border:none;outline:none;border-radius:20px;padding:15px 25px;background-color:#770f7d;color:#fff}

.description-panel{margin:20px auto;width:80vw;height:300px;background-color:#c50cce5f;border-radius:20px}.description-panel h3{padding:20px;text-align:center}.description-panel .row{display:flex;justify-content:center;align-items:center}.description-panel .row .col{padding-left:40px}.description-panel .row .col ul li{list-style:none}

